<template>
    <div class="mBg">
        <mhead-top titleHead="评价" lfIcon="el-icon-arrow-left icon" lfUrl="back" rgIcon="el-icon-shopping-cart-1 icon" rgUrl="/cart/cart"></mhead-top>
        <div class="m-evaluate">
            <div class="m-eva-top bbGray">
                <div class="m-eva-lf">用户评价（{{list.length}}）</div>
<!--                <div class="m-eva-rg"><span>99.3%</span>好评率</div>-->
            </div>
            <div class="m-eva-panel" v-for="(item,index) in list" :key="index">
                <div class="m-eva-content">
                    <div class="m-eva-tit">
                        <span>{{item.nickname}}</span>
                        <el-rate
                                class="m-eva-star"
                                :value="item.goods_rank"
                                disabled>
                        </el-rate>
                    </div>
                    <div class="m-eva-time">
                        <span>{{item.create_time}}</span>
                    </div>
                    <div class="m-eva-content">
                        {{item.content}}
                    </div>
                </div>
                <div class="m-eva-pic">
                    <div class="mEvaImg" v-for="(iitem,index) in list[index].photos" :key="index">
                        <el-image class="meImg" :src="iitem" fit="cover" :preview-src-list="list[index].photos"></el-image>
                    </div>
                </div>
            </div>

        </div>



    </div>
</template>


<script>
    import mheadTop from '../../components/mobile/headTop';
    import {getComment} from '../../service/api'
    export default {
        name: 'mGoodEvaluate',
        data(){
            return{
                spec_id:'',
                list:[]
            }
        },
        components:{
            mheadTop
        },
        mounted(){
            this.initData();
        },
        computed:{


        },
        methods:{
            async initData(){
                this.spec_id = this.$route.query.spec_id;
                // 获取产品评价
                let evaluateRes = await getComment(
                    this.spec_id,
                    '',
                    ''
                );
                this.list = evaluateRes;
                console.log(evaluateRes);
            },


        },
        created(){

        },

    }

</script>

<style lang="scss">
    @import '../../style/mixin';
    body{
        height: 100%;
    }
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        height: 100%;
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    /*评价*/
    .m-evaluate{
        background-color: #fff;
        margin-top:px2rem(20);
        padding:0 px2rem(20) px2rem(40) px2rem(20);
    }
    .m-eva-top{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        height: px2rem(104);
        line-height: px2rem(104);
    }
    .m-eva-top .icon{
        font-size: px2rem(48);
        line-height: px2rem(104);
        color:#999;
        text-align: right;
        padding-left: px2rem(4);
    }
    .m-eva-lf{
        width:300px;
    }
    .m-eva-rg{
        width: 100%;
        text-align: right;
    }
    .m-eva-rg span{
        color:$colorred;
    }
    .m-eva-tit{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        line-height: px2rem(60);
        margin-top:5px;
    }
    .m-eva-star{
        padding-top:px2rem(14);
        padding-left:px2rem(14);
    }
    .m-eva-time{
        font-size:px2rem(24);
        color:#999;
    }
    .m-eva-content{
        font-size:px2rem(28);
        color:#333;
        margin-bottom:5px;
    }
    .m-eva-pic{
        display: flex;
        flex-wrap: nowrap;
    }
    .mEvaImg{
        width:px2rem(126);
        height: px2rem(126);
        padding-right:px2rem(20);
    }
    .meImg{
        width:px2rem(126);
        height: px2rem(126);
    }
    .m-eva-panel{
        padding:px2rem(6) 0 px2rem(30) 0;
        border-bottom:1px solid $bc;
    }

</style>



